.button{
    background-color: rgba(23, green, blue, alpha);
    height: 40px;
    width: 256px;
    margin:8px;
    display: flex;
    justify-items: center;
    align-items: center;
    border-radius: 100px;
    opacity: 0.85;
}
.button >a{
    text-decoration: none;
    color: white;
    font-size: 12px;
}
.button__white{
    background-color: white;
    opacity: 0.65;

}
.button__white >a{
    color:black;

}